import { Component, OnInit, EventEmitter, Output, HostListener } from '@angular/core';

@Component({
  selector: 'app-catalog-toolbar',
  templateUrl: './catalog-toolbar.component.html',
  styleUrls: ['./catalog-toolbar.component.scss']
})
export class CatalogToolbarComponent implements OnInit {
  @Output() toggleTreeView: EventEmitter<null> = new EventEmitter();

  @HostListener('click', ['$event.target'])
  click(t) {
    this.toggleTreeView.emit();
    console.log(t);
  }

  constructor() { }

  ngOnInit() {
  }

}
